@use "./../variables/color-variables.scss";

.navigation {
  font-family: "PT Sans", sans-serif;
  position: relative;
  z-index: 1;
}

.navigation__list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.navigation__item {
  background-color: color-variables.$basic-color;
  box-shadow: 0 -1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);

  &:first-child {
    box-shadow: 0 -1px 0 rgba(1, 1, 1, 0.2);
  }

  &:last-child {
    background-image: linear-gradient(180deg, rgba(0, 0, 0, 0) 87%, rgba(0, 0, 0, 0.2) 100%);
  }
}

.navigation__link {
  display: flex;
  justify-content: center;
  padding: 17px 18px 12px;

  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  text-transform: uppercase;
  color: color-variables.$add-color-light;
  text-decoration: none;
}
